@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2");
  src: url("../fonts/opensans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2");
  src: url("../fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensanslight.woff2") format("woff2");
  src: url("../fonts/opensanslight.woff") format("woff");
  font-weight: 300;
  font-style: normal;
}
body {
  margin: 0 auto;
  padding: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  min-width: 320px;
}
a {
  text-decoration: none;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@media (min-width: 660px) {
  body {
    min-width: 660px;
  }
}
@media (min-width: 1200px) {
  body {
    width: 1200px;
  }
}
.header {
  display: flex;
  position: relative;
  text-transform: uppercase;
}
.header__image {
  position: absolute;
  top: 21px;
  left: 21px;
  z-index: 9;
  fill: #ffffff;
}
.header__image--tablet {
  display: none;
}
.header__image--desktop {
  display: none;
}
.header__logo:hover .header__image,
.header__logo:focus .header__image {
  fill: #d22856;
}
.header__logo:active .header__image {
  fill: #ffffff;
  opacity: 0.3;
}
@media (min-width: 660px) {
  .header__image {
    display: none;
  }
  .header__image--tablet {
    display: block;
    position: absolute;
    top: 27px;
    left: 20px;
    z-index: 9;
  }
  .header__image--desktop {
    display: none;
  }
}
@media (min-width: 1200px) {
  .header__image--desktop {
    display: block;
    position: absolute;
    top: 27px;
    left: 130px;
    z-index: 9;
  }
  .header__image--tablet {
    display: none;
  }
  .header__logo:hover .header__image,
  .header__logo:focus .header__image {
    fill: #d22856;
  }
  .header__logo:active .header__image {
    fill: #ffffff;
    opacity: 0.3;
  }
  .header {
    text-transform: none;
  }
}
.nav {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  min-height: 70px;
  width: 100%;
  background-color: #283645;
}
.nav__toggle {
  height: 23px;
  width: 25px;
  background-image: url("../img/icon-menu-cross.svg");
  background-color: #283645;
  background-repeat: no-repeat;
  margin-top: 20px;
  margin-right: 18px;
  border: 0;
}
@media (max-width: 1199px) {
  .body--closed .nav {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 8;
    background-color: rgba(0, 0, 0, 0.3);
  }
  .body--nojs .nav {
    position: static;
    background-color: #283645;
  }
  .body--closed .nav__toggle {
    background-image: url("../img/icon-menu-burger.svg");
    height: 24px;
    width: 51px;
  }
  .body--nojs .nav__toggle {
    display: none;
  }
}
@media (min-width: 660px) {
  .nav {
    min-height: 110px;
  }
  .nav__toggle {
    margin-top: 45px;
  }
}
@media (min-width: 1200px) {
  .nav {
    background-color: rgba(0, 0, 0, 0.3);
    position: absolute;
    z-index: 8;
  }
  .nav__toggle {
    display: none;
  }
}
