.main__competition {
  margin: 0;
  margin-top: 0;
  padding: 0;

  width: 100%;
  min-height: 170px;

  background-image: url("../img/bg-back-mobile@1x.jpg");
  background-position: top -65px left 0;

  @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
    background-image: url("../img/bg-back-mobile@2x.jpg");
  }
}

.main__title {
  margin: 0 40px;
  padding-top: 28px;

  font-size: 24px;
  font-weight: 300;
  line-height: 30px;
  color: #ffffff;
  text-align: center;
}

@media (max-width: 1199px) {
  .body--closed .main__competition {
    min-height: 240px;
  }

  .body--opened .main__competition,
  .body--nojs .main__competition {
    min-height: 170px;
  }

  .body--closed .main__title {
    padding-top: 98px;
  }

  .body--opened .main__title,
  .body--nojs .main__title {
    padding-top: 28px;
  }
}

@media (min-width: @width-tablet) {
  .main__competition {
    width: 100%;
    min-height: 328px;

    background-image: url("../img/bg-back-tablet@1x.jpg");
    background-repeat: no-repeat;
    background-position: top -110px center;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/bg-back-tablet@2x.jpg");
    }
  }

  .main__title {
    margin: 0;
    padding: 0 50px;
    padding-top: 69px;

    font-size: 50px;
    font-weight: 300;
    line-height: 60px;
  }

  .body--closed .main__competition {
    min-height: 438px;
  }

  .body--opened .main__competition,
  .body--nojs .main__competition {
    min-height: 328px;
  }

  .body--closed .main__title {
    padding-top: 179px;
  }

  .body--opened .main__title,
  .body--nojs .main__title {
    padding-top: 69px;
  }
}

@media (min-width: @width-desktop) {
  .main__competition {
    width: @width-desktop;
    min-height: 394px;

    background-image: url("../img/bg-back-desktop@1x.jpg");
    background-position: top center;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/bg-back-desktop@2x.jpg");
    }
  }

  .main__title {
    padding: 0 200px;
    padding-top: 173px;
  }

  .body--nojs .main__competition,
  .body--closed .main__competition {
    min-height: 394px;
  }

  .body--nojs .main__title {
    padding-top: 173px;
  }
}
